
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {mavonEditor} from "mavon-editor";
import "mavon-editor/dist/css/index.css";
import axios from "axios";
export default {
name:"create",
data() {
return {
title:"",//
content:"", //
toolbars: {
bold: true, // 
italic: true, // 
header: true, // 
underline: true, // 
ol: true, // 
ul: true, // 
link: true, // 
imagelink: true, // 
code: true, // code
table: true, // 
fullscreen: true, // 
htmlcode: true, // html
help: true, // 
/* 1.3.5 */
trash: true, // 
/* 1.4.2 */
/* 2.1.8 */
alignleft: true, // 
aligncenter: true, // 
alignright: true, // 
/* 2.2.1 */
subfield: true, // 
preview: true, // 
}
}
},
components:{
XTextarea,
Group,
XButton,
mavonEditor,

},
methods: {

handleTopic(){
var app=this;
this.$http.post("/topics",{
accesstoken:this.$store.state.accesstoken,
tab:"dev",
title:this.title,
content:this.content
}).then(function(res){
console.log(res);
app.$router.push("/topiclist/dev");
})
},
upLoadimgAdd(pos,file){



var params=new FormData();
params.append('smfile', file);
axios.post("https://sm.ms/api/v2/upload",params,{
headers: {
'Content-Type': 'multipart/form-data',
"Authorization": "S25cW9nGwLGGoWXT3H8A6iqrGcAyM3cK"
}

}).then(function(res){
console.log(res)
})

}
},
}



